const url = `https://rare-coral-35556.upstash.io`;



function arrayToObject(arr : any ) {
    const obj : any = {};
    for (let i = 0; i < arr.length; i += 2) {
        obj[arr[i]] = arr[i + 1]; // Set key-value pairs
    }
    return { [obj.phone] : obj };
}




const IndexPage = () => {

    const [logs, setLogs] = useState([]);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');



    const UPSTASH_REDIS_REST_URL = url;
    const UPSTASH_REDIS_REST_TOKEN = accessToken;
    const CHANNEL = "test-channel";
    const [messages, setMessages] = useState<string[]>([]);

    useEffect(() => {
        const eventSource = new EventSource(`${UPSTASH_REDIS_REST_URL}/subscribe/${CHANNEL}`);

        eventSource.onmessage = (event) => {
            setMessages((prev) => [...prev, event.data]); // Add new messages to the list
        };

        eventSource.onerror = (error) => {
            console.error("EventSource failed:", error);
        };

        return () => eventSource.close(); // Cleanup on unmount
    }, []);

    return (
        <div>
            <h1>Upstash Pub/Sub Test</h1>
            <p>Waiting for messages from Upstash...</p>
            <ul>
                {messages.map((msg, index) => (
                    <li key={index}>{msg}</li>
                ))}
            </ul>
        </div>
    );
};








